.test {
  height: 20px;
  width: 100%;
  background: #000;
}
/* Dropdown nav */ ul {
    list-style-type: none;
    margin: auto;
  
    padding: 0;
    overflow: hidden;
    background-color: #333;
  
    //position: fixed;
    //top: 20;
    max-width: 890px;
}
li {
    float: left;
}
li a, .dropbtn {
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
li a:hover, .dropdown:hover .dropbtn {
    background-color: #111;
}
li.dropdown {
    display: inline-block;
}
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}
.dropdown-content a:hover {background-color: #f1f1f1} .dropdown:hover .dropdown-content {
    display: block;
}



